"use client"

import React, { useEffect, useRef, useState, useMemo, useCallback } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import {
    FlaskConical,
    PlayCircle,
    StopCircle,
    Settings2,
    Minus,
    Hand,
    Moon,
    Sun
} from 'lucide-react';

// --- TS Definitions ---

// Ensure MediaPipe globals are typed if they are loaded via script tags (fallback) 
// or if we use the imported packages. The user requested using @mediapipe/hands and @mediapipe/camera_utils.
// We will import them dynamically or use the window globals if strict types are hard, 
// but let's try to use the classes from the packages if available.
// Since the user environment has them in package.json, we'll try standard imports.
// Note: MediaPipe packages sometimes have issues with SSR/imports in Next.js. 
// We will mock/handle them carefully or use dynamic imports if needed.
// For now, standard imports.
// Remove static imports to avoid build errors with UMD modules
// import { Hands, Results } from '@mediapipe/hands';
// import { Camera } from '@mediapipe/camera_utils';

// Helper for loading scripts
const loadScript = (src: string) => {
    return new Promise((resolve, reject) => {
        if (typeof document === 'undefined') {
            resolve(false);
            return;
        }
        if (document.querySelector(`script[src="${src}"]`)) {
            resolve(true);
            return;
        }
        const script = document.createElement('script');
        script.src = src;
        script.crossOrigin = "anonymous";
        script.onload = () => resolve(true);
        script.onerror = () => reject(new Error(`Failed to load ${src}`));
        document.body.appendChild(script);
    });
};

// Declare globals on window
declare global {
    interface Window {
        Hands: any;
        Camera: any;
    }
}

type MaterialType = 'water' | 'oil' | 'iron' | 'copper' | 'custom';

interface MaterialData {
    name: string;
    Tm: number; // Melting point
    Tb: number; // Boiling point
    colorSolid: number;
    colorLiquid: number;
    range: [number, number];
    cS: number; // Specific heat Solid
    cL: number; // Specific heat Liquid
    cG: number; // Specific heat Gas
    Lf: number; // Latent heat Fusion
    Lv: number; // Latent heat Vaporization
    densityRatio: number;
    emissive?: boolean;
}

const MATERIALS: Record<MaterialType, MaterialData> = {
    water: { name: "Nước", Tm: 0, Tb: 100, colorSolid: 0xa5f3fc, colorLiquid: 0x06b6d4, range: [-20, 150], cS: 2, cL: 4, cG: 2, Lf: 300, Lv: 800, densityRatio: 0.92 },
    oil: { name: "Dầu", Tm: -20, Tb: 200, colorSolid: 0xfef08a, colorLiquid: 0xeab308, range: [-50, 300], cS: 1.5, cL: 2, cG: 1.5, Lf: 200, Lv: 500, densityRatio: 1.0 },
    iron: { name: "Sắt", Tm: 1538, Tb: 2862, colorSolid: 0x475569, colorLiquid: 0xe64a19, range: [0, 3000], emissive: true, cS: 0.45, cL: 0.6, cG: 0.45, Lf: 270, Lv: 6000, densityRatio: 1.1 },
    copper: { name: "Đồng", Tm: 1085, Tb: 2562, colorSolid: 0xd97706, colorLiquid: 0xffedd5, range: [0, 2800], emissive: true, cS: 0.38, cL: 0.5, cG: 0.38, Lf: 205, Lv: 4800, densityRatio: 1.1 },
    custom: { name: "Tự chọn", Tm: 0, Tb: 100, colorSolid: 0xd8b4fe, colorLiquid: 0xa855f7, range: [-100, 200], cS: 2, cL: 3, cG: 2, Lf: 300, Lv: 800, densityRatio: 1.0 }
};

const SuChuyenThe = () => {
    // --- State ---
    const [selectedMaterial, setSelectedMaterial] = useState<MaterialType>('water');
    const [customProps, setCustomProps] = useState({ Tm: 0, Tb: 100 }); // Only for custom
    const [isHeating, setIsHeating] = useState(false);
    const [temp, setTemp] = useState(0); // UI Temp
    const [isPanelVisible, setPanelVisible] = useState(true);
    const [isGestureEnabled, setGestureEnabled] = useState(false);
    const [handDebug, setHandDebug] = useState("Waiting for hands...");
    const [bigTempDisplay, setBigTempDisplay] = useState<number | null>(null);
    const [theme, setTheme] = useState<'light' | 'dark'>('dark');

    // --- Refs for 3D & Physics Logic (Mutable, no re-renders) ---
    const containerRef = useRef<HTMLDivElement>(null);
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const videoRef = useRef<HTMLVideoElement>(null);
    const requestRef = useRef<number>(0);
    const controlsRef = useRef<OrbitControls | null>(null);
    const cameraRef = useRef<THREE.PerspectiveCamera | null>(null);

    // Simulation Mutable State
    const simRef = useRef({
        scene: null as THREE.Scene | null,
        renderer: null as THREE.WebGLRenderer | null,
        mats: { // Materials referencing Three.js materials
            solid: null as THREE.MeshStandardMaterial | null,
            liquid: null as THREE.MeshPhysicalMaterial | null,
            glass: null as THREE.MeshPhysicalMaterial | null,
        },
        meshes: {
            solid: null as THREE.Mesh | null,
            liquid: null as THREE.Mesh | null,
            steam: [] as THREE.Mesh[],
            glowLight: null as THREE.PointLight | null,
        },
        physics: {
            currentTemp: 0,
            simTime: 0,
            currentQ: 0, // Heat/Latent accumulation
            phaseState: 0, // 0=Solid, 1=Melt, 2=Liquid, 3=Boil, 4=Gas
            visualMeltProgress: 0,
            visualBoilProgress: 0,
            graphData: [] as { t: number, T: number }[],
            isHeating: false,
        },
        currentMatData: MATERIALS['water'],
    });

    // --- Helper Functions ---
    const getBlackBodyColor = (tempC: number) => {
        const color = new THREE.Color(0x000000);
        if (tempC < 500) return color;
        const t = Math.min(tempC, 3000);
        if (t < 1000) {
            const i = (t - 500) / 500;
            color.setRGB(i, 0, 0);
        } else if (t < 2000) {
            const i = (t - 1000) / 1000;
            color.setRGB(1, i * 0.8, 0);
        } else {
            const i = (t - 2000) / 1000;
            color.setRGB(1, 0.8 + i * 0.2, i);
        }
        return color;
    };

    // --- 3D Initialization ---
    useEffect(() => {
        if (!containerRef.current) return;

        console.log("Initializing 3D Scene...");
        try {
            // Init Scene
            const scene = new THREE.Scene();
            scene.background = new THREE.Color(theme === 'class' ? 0x0f172a : (theme === 'dark' ? 0x0f172a : 0xf8fafc));
            scene.fog = new THREE.Fog(scene.background, 10, 50);

            const camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 0.1, 100);
            camera.position.set(0, 6, 16);
            cameraRef.current = camera;

            const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
            renderer.setSize(window.innerWidth, window.innerHeight);
            renderer.setPixelRatio(window.devicePixelRatio);
            renderer.shadowMap.enabled = true;
            containerRef.current.appendChild(renderer.domElement);

            const controls = new OrbitControls(camera, renderer.domElement);
            controls.enableDamping = true;
            controls.maxDistance = 40;
            controls.minDistance = 5;
            controls.target.set(0, 2, 0);
            controlsRef.current = controls;

            // Lights
            const ambient = new THREE.AmbientLight(0xffffff, 0.6);
            scene.add(ambient);

            const dirLight = new THREE.DirectionalLight(0xffffff, 1);
            dirLight.position.set(5, 12, 8);
            dirLight.castShadow = true;
            scene.add(dirLight);

            // Grid/Plane
            const gridHelper = new THREE.GridHelper(30, 30,
                theme === 'dark' ? 0x1e293b : 0xe2e8f0,
                theme === 'dark' ? 0x1e293b : 0xe2e8f0
            );
            scene.add(gridHelper);

            const plane = new THREE.Mesh(
                new THREE.PlaneGeometry(30, 30),
                new THREE.MeshStandardMaterial({
                    color: theme === 'dark' ? 0x0f172a : 0xf8fafc,
                    roughness: 0.8,
                    metalness: 0.2
                })
            );
            plane.rotation.x = -Math.PI / 2;
            plane.receiveShadow = true;
            scene.add(plane);

            // Glow Light
            const glowLight = new THREE.PointLight(0xff4500, 0, 10);
            glowLight.position.set(0, 2, 0);
            glowLight.castShadow = true;
            scene.add(glowLight);

            // Group
            const group = new THREE.Group();
            scene.add(group);

            // 1. Beaker
            const glassMat = new THREE.MeshPhysicalMaterial({
                color: theme === 'dark' ? 0x88CCFF : 0xffffff,
                metalness: 0,
                roughness: 0.1,
                transmission: 0.9,
                transparent: true,
                opacity: theme === 'dark' ? 0.2 : 0.4,
                side: THREE.DoubleSide
            });
            const beaker = new THREE.Mesh(new THREE.CylinderGeometry(3.2, 3.2, 5, 32, 1, true), glassMat);
            beaker.position.y = 2.5;
            beaker.castShadow = true;
            group.add(beaker);

            const beakerBottom = new THREE.Mesh(new THREE.CylinderGeometry(3.2, 3.2, 0.2, 32), glassMat);
            beakerBottom.position.y = 0.1;
            beakerBottom.receiveShadow = true;
            group.add(beakerBottom);

            // 2. Substance
            const solidMat = new THREE.MeshStandardMaterial({ color: 0x06b6d4, roughness: 0.4, metalness: 0.1 });
            const solidMesh = new THREE.Mesh(new THREE.BoxGeometry(3, 3, 3), solidMat);
            solidMesh.rotation.y = Math.PI / 4;
            solidMesh.castShadow = true;
            solidMesh.receiveShadow = true;
            solidMesh.position.y = 1.7;
            group.add(solidMesh);

            const liquidMat = new THREE.MeshPhysicalMaterial({
                color: 0x06b6d4, transmission: 0.6, opacity: 0.8,
                transparent: true, roughness: 0.1, metalness: 0.1
            });
            const liquidMesh = new THREE.Mesh(new THREE.CylinderGeometry(3.1, 3.1, 1, 32), liquidMat);
            liquidMesh.position.y = 1;
            liquidMesh.scale.y = 0.01;
            group.add(liquidMesh);

            // 3. Steam
            const steamParticles: THREE.Mesh[] = [];
            const steamGeo = new THREE.SphereGeometry(0.2, 8, 8);
            const steamMat = new THREE.MeshBasicMaterial({ color: 0xffffff, transparent: true, opacity: 0.3 });
            const steamGroup = new THREE.Group();
            group.add(steamGroup);

            for (let i = 0; i < 200; i++) {
                const mesh = new THREE.Mesh(steamGeo, steamMat);
                mesh.visible = false;
                mesh.userData = { velocity: new THREE.Vector3(), age: 0, life: 100 };
                steamGroup.add(mesh);
                steamParticles.push(mesh);
            }

            // Store refs
            simRef.current.scene = scene;
            simRef.current.renderer = renderer;
            simRef.current.mats = { solid: solidMat, liquid: liquidMat, glass: glassMat };
            simRef.current.meshes = { solid: solidMesh, liquid: liquidMesh, steam: steamParticles, glowLight };

            // Handle Resize
            const handleResize = () => {
                camera.aspect = window.innerWidth / window.innerHeight;
                camera.updateProjectionMatrix();
                renderer.setSize(window.innerWidth, window.innerHeight);
            };
            window.addEventListener('resize', handleResize);

            // Cleanup
            return () => {
                window.removeEventListener('resize', handleResize);
                if (requestRef.current) cancelAnimationFrame(requestRef.current);
                if (containerRef.current) containerRef.current.removeChild(renderer.domElement);
                renderer.dispose();
                // Dispose geometries/materials ideally
            };
        } catch (e: any) {
            console.error("3D Init Error:", e);
        }
    }, []); // Run once on mount

    // --- Update Theme ---
    useEffect(() => {
        if (!simRef.current.scene) return;
        const color = theme === 'dark' ? 0x0f172a : 0xf8fafc;
        const fogColor = new THREE.Color(color);
        simRef.current.scene.background = fogColor;
        if (simRef.current.scene.fog) simRef.current.scene.fog.color = fogColor;

        if (simRef.current.mats.glass) {
            simRef.current.mats.glass.color.setHex(theme === 'dark' ? 0x88CCFF : 0xffffff);
            simRef.current.mats.glass.opacity = theme === 'dark' ? 0.2 : 0.4;
        }
    }, [theme]);

    // --- Physics & Animation Loop ---
    const updatePhysics = () => {
        const sim = simRef.current;
        const mat = sim.currentMatData;

        // Sync custom props if needed
        if (selectedMaterial === 'custom') {
            mat.Tm = customProps.Tm;
            mat.Tb = customProps.Tb;
        }

        if (sim.physics.isHeating) {
            const dt = 0.5;
            sim.physics.simTime += dt;
            const dQ = 5;
            let temp = sim.physics.currentTemp;
            const { Tm, Tb, cS, cL, cG, Lf, Lv } = mat;

            // Logic matching source
            if (temp < Tm) {
                // Solid Heating
                sim.physics.phaseState = 0;
                sim.physics.currentTemp += dQ / cS * 0.1;
                if (sim.physics.currentTemp >= Tm) {
                    sim.physics.currentTemp = Tm;
                    sim.physics.currentQ = 0;
                }
            } else if (Math.abs(temp - Tm) < 0.1 && sim.physics.phaseState <= 1) {
                // Melting
                sim.physics.phaseState = 1;
                sim.physics.currentQ += dQ;
                sim.physics.visualMeltProgress = Math.min(1, sim.physics.currentQ / Lf);
                if (sim.physics.currentQ >= Lf) {
                    sim.physics.phaseState = 2;
                    sim.physics.currentQ = 0;
                    sim.physics.currentTemp += 0.5;
                    sim.physics.visualMeltProgress = 1;
                }
            } else if (temp < Tb) {
                // Liquid Heating
                sim.physics.phaseState = 2;
                sim.physics.currentTemp += dQ / cL * 0.1;
                if (sim.physics.currentTemp >= Tb) {
                    sim.physics.currentTemp = Tb;
                    sim.physics.currentQ = 0;
                }
            } else if (Math.abs(temp - Tb) < 0.1 && sim.physics.phaseState <= 3) {
                // Boiling
                sim.physics.phaseState = 3;
                sim.physics.currentQ += dQ;
                const scaledLv = Lv / 4; // Speed up
                sim.physics.visualBoilProgress = Math.min(1, sim.physics.currentQ / scaledLv);
                if (sim.physics.currentQ >= scaledLv) {
                    sim.physics.phaseState = 4;
                    sim.physics.currentTemp += 0.5;
                    sim.physics.visualBoilProgress = 1;
                }
            } else {
                // Gas Heating
                sim.physics.phaseState = 4;
                sim.physics.currentTemp += dQ / cG * 0.1;
                if (sim.physics.currentTemp > mat.range[1]) {
                    sim.physics.currentTemp = mat.range[1];
                }
            }

            // Record Graph Data
            if (Math.floor(sim.physics.simTime) % 2 === 0) {
                sim.physics.graphData.push({ t: sim.physics.simTime, T: sim.physics.currentTemp });
                if (sim.physics.graphData.length > 200) sim.physics.graphData.shift(); // Keep buffer sane
            }

            // Sync React State roughly (every 5 frames or so to avoid too many re-renders?)
            // Or just do it every frame if simple. 
            // We'll update the temp state so slider moves.
            setTemp(sim.physics.currentTemp);
        } else {
            // Update internal physics temp from UI if not heating (Manual Slider)
            // Handled via setTemp/useEffect or direct assignment? 
            // Let's rely on the ref for the loop, and update ref when state changes.
        }
    };

    const spawnSteam = () => {
        const sim = simRef.current;
        const p = sim.meshes.steam.find(p => !p.visible);
        if (!p || !sim.meshes.liquid) return;

        p.visible = true;
        const r = (Math.random() - 0.5) * 4;
        const theta = Math.random() * Math.PI * 2;
        const radius = Math.random() * 2.5;

        p.position.set(
            Math.cos(theta) * radius,
            sim.meshes.liquid.visible ? (sim.meshes.liquid.position.y * 2) + 0.2 : 0.5,
            Math.sin(theta) * radius
        );

        p.userData.age = 0;
        p.userData.life = 100 + Math.random() * 50;
        p.userData.velocity.set(
            (Math.random() - 0.5) * 0.02,
            0.05 + Math.random() * 0.05,
            (Math.random() - 0.5) * 0.02
        );
        p.scale.setScalar(0.5 + Math.random());
    };

    const update3DObjects = (time: number) => {
        const sim = simRef.current;
        const mat = sim.currentMatData;
        const tempVal = sim.physics.currentTemp;
        const { Tm, Tb } = mat;

        // Colors
        if (sim.mats.solid && sim.mats.liquid) {
            sim.mats.solid.color.setHex(mat.colorSolid);
            sim.mats.liquid.color.setHex(mat.colorLiquid);
        }

        // Helpers
        const isHeating = sim.physics.isHeating;
        const meltRange = 10;
        const boilRange = 10;

        // --- Melting Logic ---
        let meltProgress = 0;
        if (isHeating) {
            if (tempVal < Tm) meltProgress = 0;
            else if (sim.physics.phaseState === 1) meltProgress = sim.physics.visualMeltProgress;
            else if (tempVal >= Tm && sim.physics.phaseState >= 2) meltProgress = 1;
        } else {
            if (tempVal < Tm) meltProgress = 0;
            else if (tempVal > Tm + meltRange) meltProgress = 1;
            else meltProgress = (tempVal - Tm) / meltRange;
        }

        const solidScale = 1 - meltProgress;
        if (sim.meshes.solid) {
            if (solidScale > 0.01) {
                sim.meshes.solid.visible = true;
                sim.meshes.solid.scale.setScalar(solidScale);
                sim.meshes.solid.position.y = 0.2 + (1.5 * solidScale);
                sim.meshes.solid.rotation.y = time * 0.0005;
            } else {
                sim.meshes.solid.visible = false;
            }
        }

        // --- Boiling Logic ---
        let boilProgress = 0;
        if (isHeating) {
            if (tempVal < Tb) boilProgress = 0;
            else if (sim.physics.phaseState === 3) boilProgress = sim.physics.visualBoilProgress;
            else if (tempVal >= Tb && sim.physics.phaseState === 4) boilProgress = 1;
        } else {
            if (tempVal < Tb) boilProgress = 0;
            else if (tempVal > Tb + boilRange) boilProgress = 1;
            else boilProgress = (tempVal - Tb) / boilRange;
        }

        // --- Liquid Logic ---
        let liquidAmount = meltProgress * (1 - boilProgress);
        if (sim.meshes.liquid) {
            if (liquidAmount > 0.01) {
                sim.meshes.liquid.visible = true;
                const ratio = mat.densityRatio || 1.0;
                const baseHeight = 0.9;
                const targetHeight = baseHeight * ratio * liquidAmount;
                sim.meshes.liquid.scale.y = Math.max(0.01, targetHeight);
                sim.meshes.liquid.position.y = 0.2 + (targetHeight / 2);
            } else {
                sim.meshes.liquid.visible = false;
            }
        }

        // --- Emissive ---
        if (mat.emissive && tempVal > 400 && sim.mats.solid && sim.mats.liquid) {
            const glowColor = getBlackBodyColor(tempVal);
            const intensity = Math.min(2, Math.max(0, (tempVal - 400) / 600));
            sim.mats.solid.emissive.copy(glowColor);
            sim.mats.solid.emissiveIntensity = intensity;

            // Adjust transmission for metals
            sim.mats.liquid.transmission = 0;
            sim.mats.liquid.opacity = 1;
            sim.mats.liquid.emissive.copy(glowColor);
            sim.mats.liquid.emissiveIntensity = intensity;

            if (sim.meshes.glowLight) {
                sim.meshes.glowLight.color.copy(glowColor);
                sim.meshes.glowLight.intensity = intensity * 1.5;
            }
        } else if (sim.mats.solid && sim.mats.liquid) {
            sim.mats.solid.emissive.setHex(0x000000);
            sim.mats.liquid.emissive.setHex(0x000000);
            sim.mats.solid.emissiveIntensity = 0;
            sim.mats.liquid.emissiveIntensity = 0;
            sim.mats.liquid.transmission = 0.6; // Reset to water/oil
            sim.mats.liquid.opacity = 0.8;
            if (sim.meshes.glowLight) sim.meshes.glowLight.intensity = 0;
        }

        // --- Steam ---
        if (tempVal > Tb - 20) {
            const spawnRate = Math.max(0.1, (tempVal - (Tb - 20)) / 50);
            if (Math.random() < spawnRate) spawnSteam();
        }

        sim.meshes.steam.forEach(p => {
            if (!p.visible) return;
            p.position.add(p.userData.velocity);
            p.userData.age++;
            const lifeRatio = p.userData.age / p.userData.life;
            if (Array.isArray(p.material)) return; // TS guard
            (p.material as THREE.MeshBasicMaterial).opacity = 0.4 * (1 - lifeRatio);
            p.scale.multiplyScalar(1.005);
            if (p.userData.age >= p.userData.life) p.visible = false;
        });
    };

    const drawGraph = () => {
        if (!canvasRef.current) return;
        const ctx = canvasRef.current.getContext('2d');
        if (!ctx) return;

        const w = canvasRef.current.width;
        const h = canvasRef.current.height;
        const isDark = theme === 'dark';

        ctx.clearRect(0, 0, w, h);
        ctx.fillStyle = isDark ? "#1e293b" : "#f8fafc";
        ctx.fillRect(0, 0, w, h);

        ctx.strokeStyle = "#94a3b8";
        ctx.lineWidth = 1;
        ctx.beginPath();
        ctx.moveTo(30, 10); ctx.lineTo(30, h - 20);
        ctx.moveTo(30, h - 20); ctx.lineTo(w - 10, h - 20);
        ctx.stroke();

        ctx.fillStyle = "#64748b";
        ctx.font = "10px monospace";
        ctx.fillText("T°C", 5, 10);
        ctx.fillText("Time", w - 30, h - 5);

        const data = simRef.current.physics.graphData;
        if (data.length < 2) return;

        const maxTime = Math.max(200, data[data.length - 1].t);
        const mat = simRef.current.currentMatData;
        const minT = mat.range[0];
        const maxT = mat.range[1];
        const rangeT = maxT - minT;

        ctx.strokeStyle = "#06b6d4";
        ctx.lineWidth = 2;
        ctx.beginPath();
        data.forEach((p, i) => {
            const x = 30 + (p.t / maxTime) * (w - 40);
            const y = (h - 20) - ((p.T - minT) / rangeT) * (h - 30);
            if (i === 0) ctx.moveTo(x, y);
            else ctx.lineTo(x, y);
        });
        ctx.stroke();
    };

    // Animation Loop
    useEffect(() => {
        const animate = (time: number) => {
            requestRef.current = requestAnimationFrame(animate);
            try {
                updatePhysics();
                update3DObjects(time);
                drawGraph();
                if (controlsRef.current) controlsRef.current.update();
                if (simRef.current.renderer && simRef.current.scene && cameraRef.current) {
                    simRef.current.renderer.render(simRef.current.scene, cameraRef.current);
                }
            } catch (e) {
                console.error("Animation Loop Error:", e);
                cancelAnimationFrame(requestRef.current);
            }
        };
        requestRef.current = requestAnimationFrame(animate);
        return () => cancelAnimationFrame(requestRef.current);
    }, [selectedMaterial]); // Dependencies? Actually refs cover mutable state, so we don't need to re-bind often.
    // However, selectedMaterial change logic handles physics reset elsewhere.

    // --- UI Handlers ---
    const handleMaterialChange = (type: MaterialType) => {
        setSelectedMaterial(type);
        simRef.current.currentMatData = MATERIALS[type];
        // Reset Logic
        setIsHeating(false);
        simRef.current.physics.isHeating = false;
        simRef.current.physics.simTime = 0;
        simRef.current.physics.graphData = [];
        simRef.current.physics.visualMeltProgress = 0;
        simRef.current.physics.visualBoilProgress = 0;

        // Initial Temp
        const initTemp = MATERIALS[type].range[0];
        simRef.current.physics.currentTemp = initTemp;
        setTemp(initTemp);
    };

    const toggleHeat = () => {
        const newState = !isHeating;
        setIsHeating(newState);
        simRef.current.physics.isHeating = newState;
        if (newState) {
            // Start
            simRef.current.physics.graphData = [{ t: 0, T: simRef.current.physics.currentTemp }];
        }
    };

    const handleTempSlider = (val: number) => {
        if (isHeating) return; // Prevent manual change while heating
        setTemp(val);
        simRef.current.physics.currentTemp = val;
    };

    // --- MediaPipe Hand Gestures ---
    useEffect(() => {
        if (!isGestureEnabled || !videoRef.current) {
            return;
        }

        let camera: any = null;
        let hands: any = null;
        let active = true;
        let prevHandPos = { x: 0, y: 0 };

        const startHands = async () => {
            try {
                setHandDebug("Loading AI Models...");
                await loadScript('https://cdn.jsdelivr.net/npm/@mediapipe/camera_utils/camera_utils.js');
                await loadScript('https://cdn.jsdelivr.net/npm/@mediapipe/hands/hands.js');

                if (!active) return;

                if (!window.Hands || !window.Camera) {
                    throw new Error("Failed to load MediaPipe globals");
                }

                hands = new window.Hands({ locateFile: (file: string) => `https://cdn.jsdelivr.net/npm/@mediapipe/hands/${file}` });
                hands.setOptions({
                    maxNumHands: 2,
                    modelComplexity: 1,
                    minDetectionConfidence: 0.5,
                    minTrackingConfidence: 0.5
                });

                hands.onResults((results: any) => {
                    if (!active) return;
                    const lm = results.multiHandLandmarks;
                    if (lm && lm.length > 0) {
                        setHandDebug(`Detected ${lm.length} hand(s)`);

                        if (lm.length === 2) {
                            // 2 HANDS -> ZOOM
                            const h1 = lm[0][9];
                            const h2 = lm[1][9];
                            const dist = Math.hypot(h1.x - h2.x, h1.y - h2.y);
                            const t = (Math.max(0.1, Math.min(0.8, dist)) - 0.1) / 0.7;
                            const targetDist = 40 - (t * 35);
                            if (cameraRef.current && controlsRef.current) {
                                const curDist = cameraRef.current.position.distanceTo(controlsRef.current.target);
                                const newDist = curDist + (targetDist - curDist) * 0.1;
                                const dir = new THREE.Vector3().copy(cameraRef.current.position).sub(controlsRef.current.target).normalize();
                                cameraRef.current.position.copy(controlsRef.current.target).add(dir.multiplyScalar(newDist));
                            }
                            setHandDebug(`👐 ZOOM: ${(t * 100).toFixed(0)}%`);
                        } else {
                            // 1 HAND
                            const l = lm[0];
                            const dist = (i: number, j: number) => Math.hypot(l[i].x - l[j].x, l[i].y - l[j].y);
                            const isFolded = (tip: number, pip: number) => dist(tip, 0) < dist(pip, 0);

                            const indexOpen = !isFolded(8, 6);
                            const middleOpen = !isFolded(12, 10);
                            const ringOpen = !isFolded(16, 14);
                            const pinkyOpen = !isFolded(20, 18);

                            // L-Shape / Pinch Logic (Thumb + Index)
                            // Source logic: !isFolded(8, 6) && isFolded(12, 10)... means Index Open, others Closed.
                            // Pinch distance typically 4 to 8.
                            const sourceLControl = !isFolded(8, 6) && isFolded(12, 10) && isFolded(16, 14) && isFolded(20, 18);
                            const isOpen = indexOpen && middleOpen && ringOpen && pinkyOpen;
                            const pinchDist = dist(4, 8);

                            if (sourceLControl) {
                                const minP = 0.02, maxP = 0.20;
                                const percent = Math.max(0, Math.min(1, (pinchDist - minP) / (maxP - minP)));
                                const mat = simRef.current.currentMatData;
                                const val = mat.range[0] + (mat.range[1] - mat.range[0]) * percent;

                                if (!simRef.current.physics.isHeating) {
                                    setTemp(val);
                                    simRef.current.physics.currentTemp = val;
                                    setBigTempDisplay(val);
                                }
                                setHandDebug(`👌 TEMP: ${val.toFixed(0)}°C`);
                            } else if (isOpen) {
                                setBigTempDisplay(null);
                                const handX = 1 - l[9].x;
                                const handY = l[9].y;
                                const dx = handX - prevHandPos.x;
                                const dy = handY - prevHandPos.y;

                                if (Math.abs(dx) > 0.002 && cameraRef.current && controlsRef.current) {
                                    const offset = new THREE.Vector3().copy(cameraRef.current.position).sub(controlsRef.current.target);
                                    const spherical = new THREE.Spherical().setFromVector3(offset);
                                    spherical.theta -= dx * 5;
                                    spherical.phi -= dy * 5;
                                    spherical.phi = Math.max(0.1, Math.min(Math.PI - 0.1, spherical.phi));
                                    cameraRef.current.position.copy(controlsRef.current.target).add(new THREE.Vector3().setFromSpherical(spherical));
                                    cameraRef.current.lookAt(controlsRef.current.target);
                                }
                                prevHandPos = { x: handX, y: handY };
                                setHandDebug("🔄 ROTATE");
                            } else {
                                setBigTempDisplay(null);
                                setHandDebug("Waiting...");
                            }
                        }
                    } else {
                        setHandDebug("No hand detected");
                        setBigTempDisplay(null);
                    }
                });

                if (videoRef.current && window.Camera) {
                    camera = new window.Camera(videoRef.current, {
                        onFrame: async () => {
                            if (hands && videoRef.current) await hands.send({ image: videoRef.current });
                        },
                        width: 320,
                        height: 240
                    });
                    camera.start();
                }

            } catch (err: any) {
                console.error(err);
                if (active) setHandDebug("Error: " + err.message);
            }
        };

        startHands();

        return () => {
            active = false;
            if (camera) camera.stop();
            if (hands) hands.close();
        };
    }, [isGestureEnabled]);

    // --- Render Helpers ---
    const getPhaseLabel = () => {
        const t = simRef.current.physics.currentTemp;
        const mat = simRef.current.currentMatData;
        if (t < mat.Tm) return { text: "SOLID", color: "bg-blue-100 text-blue-600", desc: "Rắn: Hình dạng cố định" };
        if (t >= mat.Tm && t < mat.Tb) return { text: "LIQUID", color: "bg-cyan-100 text-cyan-600", desc: "Lỏng: Hình dạng đáy bình" };
        return { text: "GAS", color: "bg-red-100 text-red-600", desc: "Khí: Bay hơi tự do" };
    };

    const phase = getPhaseLabel();

    return (
        <div className={`relative w-full h-screen overflow-hidden ${theme === 'dark' ? 'dark' : ''}`}>

            {/* Background Container for 3D */}
            <div
                ref={containerRef}
                className="absolute inset-0 z-0 bg-slate-50 dark:bg-slate-900 transition-colors duration-500"
            />

            {/* Theme Toggle */}
            <div className="absolute top-6 right-6 z-10">
                <button
                    onClick={() => setTheme(t => t === 'dark' ? 'light' : 'dark')}
                    className="p-3 bg-white/20 hover:bg-white/40 backdrop-blur-md rounded-full text-slate-800 dark:text-white shadow-lg border border-white/20 transition-all"
                >
                    {theme === 'dark' ? <Moon size={24} /> : <Sun size={24} />}
                </button>
            </div>

            {/* Big Temp Display (Gesture) */}
            {bigTempDisplay !== null && isGestureEnabled && (
                <div className="fixed top-6 right-28 text-6xl font-bold text-white drop-shadow-lg z-40 font-mono pointer-events-none animate-pulse">
                    {bigTempDisplay.toFixed(0)}°C
                </div>
            )}

            {/* Panel Toggle Button */}
            {!isPanelVisible && (
                <button
                    onClick={() => setPanelVisible(true)}
                    className="absolute bottom-6 left-6 z-40 bg-cyan-500 hover:bg-cyan-600 text-white p-3 rounded-full shadow-lg transition-transform hover:scale-110"
                >
                    <Settings2 />
                </button>
            )}

            {/* UI Panel */}
            {isPanelVisible && (
                <div className="absolute top-5 right-5 sm:right-20 w-[380px] max-h-[90vh] flex flex-col bg-white/95 dark:bg-slate-900/90 backdrop-blur-xl rounded-xl shadow-2xl z-50 transition-colors border border-slate-200 dark:border-slate-700">
                    <div className="flex items-center justify-between p-4 border-b border-slate-200 dark:border-slate-700 cursor-move">
                        <h1 className="text-lg font-bold flex items-center gap-2 text-slate-800 dark:text-white uppercase tracking-wider">
                            <FlaskConical className="text-cyan-500" /> Phân Tích
                        </h1>
                        <div className="flex items-center gap-2">
                            <div className={`text-xs font-mono px-2 py-0.5 rounded font-bold ${phase.color}`}>
                                {phase.text}
                            </div>
                            <button onClick={() => setPanelVisible(false)} className="text-slate-400 hover:text-red-500">
                                <Minus size={20} />
                            </button>
                        </div>
                    </div>

                    <div className="p-4 overflow-y-auto flex-1 space-y-4">
                        {/* Selector */}
                        <div className="grid grid-cols-2 gap-4">
                            <div>
                                <label className="block text-xs font-semibold mb-1 text-slate-500 dark:text-slate-400">Chọn Chất</label>
                                <select
                                    value={selectedMaterial}
                                    onChange={(e) => handleMaterialChange(e.target.value as MaterialType)}
                                    className="w-full p-2 rounded-lg bg-slate-100 dark:bg-slate-800 border-none text-slate-700 dark:text-slate-200 text-sm outline-none focus:ring-2 focus:ring-cyan-500"
                                >
                                    {Object.entries(MATERIALS).map(([k, v]) => (
                                        <option key={k} value={k}>{v.name}</option>
                                    ))}
                                </select>
                            </div>
                        </div>

                        {/* Custom Inputs */}
                        {selectedMaterial === 'custom' && (
                            <div className="grid grid-cols-2 gap-4 p-3 bg-slate-50 dark:bg-slate-800/50 rounded-lg">
                                <div>
                                    <label className="text-xs text-slate-500 mb-1 block">T.Nóng chảy</label>
                                    <input type="number" value={customProps.Tm} onChange={e => setCustomProps({ ...customProps, Tm: +e.target.value })} className="w-full p-1.5 rounded text-sm bg-white dark:bg-slate-700 dark:text-white border dark:border-slate-600" />
                                </div>
                                <div>
                                    <label className="text-xs text-slate-500 mb-1 block">T.Sôi</label>
                                    <input type="number" value={customProps.Tb} onChange={e => setCustomProps({ ...customProps, Tb: +e.target.value })} className="w-full p-1.5 rounded text-sm bg-white dark:bg-slate-700 dark:text-white border dark:border-slate-600" />
                                </div>
                            </div>
                        )}

                        {/* Graph */}
                        <div className="bg-slate-50 dark:bg-slate-800 rounded border border-slate-200 dark:border-slate-700 p-1">
                            <canvas ref={canvasRef} width={300} height={150} className="w-full h-auto" />
                        </div>

                        {/* Controls */}
                        <div className="flex gap-2">
                            <button
                                onClick={toggleHeat}
                                className={`flex-1 py-2 rounded font-bold text-white flex justify-center items-center gap-2 transition-colors ${isHeating ? 'bg-red-500 hover:bg-red-600' : 'bg-cyan-600 hover:bg-cyan-700'
                                    }`}
                            >
                                {isHeating ? <><StopCircle size={20} /> Dừng</> : <><PlayCircle size={20} /> Gia nhiệt</>}
                            </button>
                            <button
                                onClick={() => setGestureEnabled(!isGestureEnabled)}
                                className={`w-12 flex items-center justify-center rounded transition-colors ${isGestureEnabled ? 'bg-cyan-500 text-white shadow shadow-cyan-500/50' : 'bg-slate-100 dark:bg-slate-700 text-slate-500'
                                    }`}
                                title="Bật điều khiển tay"
                            >
                                <Hand size={20} />
                            </button>
                        </div>

                        {/* Slider */}
                        <div>
                            <label className="flex justify-between text-sm font-semibold mb-2 text-slate-600 dark:text-slate-300">
                                <span>Nhiệt Độ (T)</span>
                                <span className="font-mono text-cyan-500 text-lg">{temp.toFixed(0)}°C</span>
                            </label>
                            <input
                                type="range"
                                min={simRef.current.currentMatData.range[0]}
                                max={simRef.current.currentMatData.range[1]}
                                value={temp}
                                disabled={isHeating}
                                onChange={(e) => handleTempSlider(Number(e.target.value))}
                                className="w-full h-3 bg-gradient-to-r from-blue-500 via-purple-500 to-red-500 rounded-lg appearance-none cursor-pointer disabled:opacity-50"
                            />
                            <div className="flex justify-between mt-2 text-[10px] font-mono text-slate-400">
                                <span>{simRef.current.currentMatData.range[0]}°C</span>
                                <span className="text-blue-400">Tm: {simRef.current.currentMatData.Tm}</span>
                                <span className="text-red-400">Tb: {simRef.current.currentMatData.Tb}</span>
                                <span>{simRef.current.currentMatData.range[1]}°C</span>
                            </div>
                        </div>

                        {/* Info */}
                        <div className="text-xs text-slate-500 dark:text-slate-400 italic">
                            {phase.desc}
                        </div>
                    </div>
                </div>
            )}

            {/* Webcam Feed & Guide */}
            <div className={`absolute bottom-5 right-5 z-40 transition-all duration-300 ${!isGestureEnabled ? 'translate-y-96 opacity-0' : 'translate-y-0 opacity-100'}`}>
                <video
                    ref={videoRef}
                    className="w-40 h-32 bg-black rounded-xl border-2 border-cyan-500 shadow-[0_0_15px_rgba(6,182,212,0.5)] object-cover scale-x-[-1]"
                    autoPlay
                    playsInline
                    muted
                />
                <div className="absolute bottom-36 right-0 bg-slate-900/90 backdrop-blur-md p-3 rounded-xl border border-white/10 text-white w-48 text-xs">
                    <div className="flex items-center gap-2 mb-2 font-bold text-cyan-400">
                        <span className="w-2 h-2 rounded-full bg-cyan-400 animate-pulse"></span>
                        ĐIỀU KHIỂN TAY
                    </div>
                    <div className="space-y-2">
                        <div className="flex gap-2 items-center">
                            <span className="text-lg">👐</span>
                            <div>
                                <strong>2 Tay</strong>
                                <div className="text-slate-400">Zoom ra/vào</div>
                            </div>
                        </div>
                        <div className="flex gap-2 items-center">
                            <span className="text-lg">👌</span>
                            <div>
                                <strong>Chụm (L)</strong>
                                <div className="text-slate-400">Chỉnh nhiệt độ</div>
                            </div>
                        </div>
                        <div className="flex gap-2 items-center">
                            <span className="text-lg">🖐</span>
                            <div>
                                <strong>Mở tay</strong>
                                <div className="text-slate-400">Xoay Camera</div>
                            </div>
                        </div>
                        <div className="mt-2 pt-2 border-t border-white/10 font-mono text-cyan-200">
                            {handDebug}
                        </div>
                    </div>
                </div>
            </div>

        </div>
    );
};

export default SuChuyenThe;
